<template>
  <div style="display: flex;flex-direction: column;align-items: stretch; " class="editor1 editor2"  >
    <el-row style="padding:5px;">
      <el-col :md="2">KEY</el-col>
      <el-col :md="21">
        <el-input
          v-model="redisKey"
          placeholder=""/>
      </el-col>
    </el-row>
    <el-row style="padding:5px;">
      <el-col :md="2">TTL</el-col>
      <el-col :md="21">
        <el-input
        v-model="TTL"
        placeholder=""/>
      </el-col>
    </el-row>
    <el-row style="padding:5px;">
      <el-col :md="2">VALUE</el-col>
      <el-col :md="21">
        <vue-json-editor
          v-model="jsonStr"
          :show-btns="false"
          :mode="'code'"
          lang="zh"
          @json-change="onJsonChange"
          @json-save="onJsonSave"
          @has-error="onError">
        </vue-json-editor>
      </el-col>
    </el-row>

    <div style="width: 100%;float:right;margin-top:10px;">
      <el-button @click="searchObj">查询</el-button>
      <el-button type="primary" @click="saveObj">保存</el-button>
<!--      <el-button @click="$refs['editor'].formatCode()">格式化</el-button>-->
    </div>

  </div>
</template>

<script>



import vueJsonEditor from 'vue-json-editor'

export default {
  name: "CodeEditor",
  components:{vueJsonEditor},
  data() {
    return {
      redisKey:'',
      TTL:'-1',
      hasError:false,   //false是没错
      jsonStr: {}
    }
  },
  methods: {
    onJsonChange (value) {
      console.log('value:', value);
      try{
        JSON.stringify(value);
        this.hasError = false;
      }catch(E){
        this.hasError = true;
      }

      // console.info(this.hasError);
    },
    onJsonSave (value) {
      console.log('value:', value);
    },
    onError (value) {
      console.log('value:', value);
       try{
         JSON.parse(value);
         this.hasError = false;
       }catch(E){
         this.hasError = true;
       }

        // console.info(this.hasError);

    },
    // 检查json
    saveObj(){
      if (this.hasError === true){
        this.$message.error("json验证失败")
        // return false;
      } else {
        // console.info("保存数据："+JSON.stringify( this.jsonStr) );
        // this.$message.success("json验证成功")
        // return true;
        ///////////
        const loading = this.$loading({
          lock: true,
          text: '提交中...',
          target: '.main',
          spinner: 'el-icon-loading',

          background: 'rgba(0, 0, 0, .7)'
        });

        var p1 = this.customGlobal.postHttpClient(
          this.customGlobal.serverUrl.postSaveCommonAccessRedis,
          this.customGlobal.serverUrl.orgBaseURI,
          {"Content-Type": "application/json"},
          {key:this.redisKey,value:JSON.stringify( this.jsonStr ),ttl:this.TTL});
        p1.then((res) => {
          loading.close();
          if(res.data.status==='success'){
            this.$message.success(res.data.msg);
          }else{
            console.info('data:'+JSON.stringify(res.data));
            if(res.data.msg==='请登录认证'){
              this.$router.push({path:'/login'});
            }
          }
          // console.log("异步调用成功返回:"+JSON.stringify(res));

        },error=>{
          console.log("异步调用出错:"+JSON.stringify(error));
          loading.close();

          this.$alert(JSON.stringify(error.message), '出错!', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        }).catch((res) => {
          console.log("异步调用失败返回:"+JSON.stringify(res));
          loading.close();


          this.$alert(JSON.stringify(res.message), '出错', {
            confirmButtonText: '确定',
            callback: action => {

            }
          });
        });
        ///////////
      }
    },
    searchObj(){
      const loading = this.$loading({
        lock: true,
        text: '查询中...',
        target: '.main',
        spinner: 'el-icon-loading',

        background: 'rgba(0, 0, 0, .7)'
      });

      var p1 = this.customGlobal.postHttpClient(
        this.customGlobal.serverUrl.postQueryKeyCommonAccessRedis,
        this.customGlobal.serverUrl.orgBaseURI,
        {"Content-Type": "application/json"},
        {key:this.redisKey});
      p1.then((res) => {
        loading.close();
        if(res.data.status==='success'){
          this.jsonStr = JSON.parse(res.data.msg);
          this.TTL = res.data.ttl;
        }else{
          console.info('data:'+JSON.stringify(res.data));
          if(res.data.msg==='请登录认证'){
            this.$router.push({path:'/login'});
          }
        }
        // console.log("异步调用成功返回:"+JSON.stringify(res));

      },error=>{
        console.log("异步调用出错:"+JSON.stringify(error));
        loading.close();

        this.$alert(JSON.stringify(error.message), '出错!', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      }).catch((res) => {
        console.log("异步调用失败返回:"+JSON.stringify(res));
        loading.close();


        this.$alert(JSON.stringify(res.message), '出错', {
          confirmButtonText: '确定',
          callback: action => {

          }
        });
      });
    }


  }
}
</script>

<style scoped>
.editor1 /deep/ .jsoneditor-vue {
  min-width: 100% !important;
  height: 350px!important;
  text-align: left!important;

}
.editor2 /deep/ .jsoneditor-poweredBy{
   display: none!important;
}
</style>



